import {Component} from 'react';
import config from "../../../config.json";
import BackendUrls from "../../../constants/BackendUrls.json";
import {AddCenter, GetAllCenters, RemoveCenter} from "../../vaccinesessions/IndexDbManager";
import axios from "axios";

class SyncPinCodeManager extends Component {
    constructor(props) {
        super(props);
        this.IsTrackingContains = this.IsTrackingContains.bind(this);
        this.RemoveFromTracking = this.RemoveFromTracking.bind(this);
        this.RemovePinCodeFromBackend = this.RemovePinCodeFromBackend.bind(this);
        this.SetTrackingCentersFromDbIndex = this.SetTrackingCentersFromDbIndex.bind(this);
        this.SyncPinCodeWithBackend = this.SyncPinCodeWithBackend.bind(this);
        this.AddToTracking = this.AddToTracking.bind(this);
        this.SetTrackingCentersFromDbIndex();
    }

    SetTrackingCentersFromDbIndex(){
        GetAllCenters().then(item =>{
            this.props.setTrackingCenters(item);
        })
    }

    SyncPinCodeWithBackend(){
        if(!this.props.user.token) return;
        const url = config.BACKEND_BASE_URL + BackendUrls.SYNC_PIN_CODE_URL;
        const options = {
            headers: {
                'Content-Type': 'application/json',
                'Authorization': 'Token ' + this.props.user.token
            }
        };

        GetAllCenters().then(pin_codes=>{
            axios.post(url, {"pin_codes": pin_codes.map(p=>p.pin_code)}, options)
                .then(res => {
                    res.data["pin_codes"].forEach(pin_code => {
                        AddCenter(pin_code);
                    })
                    this.SetTrackingCentersFromDbIndex();
                })
                .catch(error => {
                    console.log(error);
                });
        })
    }

    IsTrackingContains(pin_code){
        return (this.props.vaccine.tracking_centers.findIndex(center => center.pin_code === pin_code) >=0);
    }

    RemovePinCodeFromBackend(pin_code){
        const url = config.BACKEND_BASE_URL + BackendUrls.REMOVE_PIN_CODE_URL;
        const options = {
            headers: {
                'Content-Type': 'application/json',
                'Authorization': 'Token ' + this.props.user.token
            }
        };

        axios.post(url,{"pin_code": pin_code}, options)
            .then(res => {
                console.debug(res);
            })
            .catch(error => {
                console.log(error);
            });
    }

    RemoveFromTracking(pin_code) {
        if(!this.IsTrackingContains(pin_code)) return;
        RemoveCenter(pin_code);

        let tracking_center = this.props.vaccine.tracking_centers;
        for (let i = tracking_center.findIndex(it => it.pin_code === pin_code); i < tracking_center.length -1; i++) {
            tracking_center[i] = tracking_center[i+1];
        }
        tracking_center.pop();

        console.log(tracking_center);
        this.props.setTrackingCenters(tracking_center);
        this.RemovePinCodeFromBackend(pin_code);
    }

    AddToTracking(pin_code) {
        if(this.IsTrackingContains(pin_code)) return;
        AddCenter(pin_code);
        let tracking_center = this.props.vaccine.tracking_centers;
        tracking_center.push({pin_code: pin_code});
        this.props.setTrackingCenters(tracking_center)
        this.AddCentersToBackend(pin_code);
    }

    AddCentersToBackend(pin_code){
        const url = config.BACKEND_BASE_URL + BackendUrls.ADD_PIN_CODE_URL;
        const options = {
            headers: {
                'Content-Type': 'application/json',
                'Authorization': 'Token ' + this.props.user.token
            }
        };

        axios.post(url,{"pin_code": pin_code}, options)
            .then(res => {
                console.log(res);
            })
            .catch(error => {
                console.log(error);
            });
    }
}

export default SyncPinCodeManager;