import React, {Component} from 'react';
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom'

import './App.css';
import GetVaccineSession from "../vaccinesessions/GetVaccineSession";
import {initDB} from "react-indexed-db";
import {DBConfig} from "../vaccinesessions/IndexDbManager";
import config from "../../config.json";

const indexedDB = window.indexedDB || (window).mozIndexedDB || (window).webkitIndexedDB || (window).msIndexedDB;

function deleteIfOldDatabase(){
    let openDBRequest = indexedDB.open('cowin', config.DB_VERSION);
    openDBRequest.onupgradeneeded = function (event){
        let database = openDBRequest.result;
        if(database.objectStoreNames.contains("centers")){
            if(event.oldVersion) {
                database.deleteObjectStore("centers");
                let store = database.createObjectStore("centers", {keyPath:"pin_code"});
                store.createIndex('pin_code', 'pin_code', {unique: true});
            }
        } else {
            let store = database.createObjectStore("centers", {keyPath: "pin_code"});
            store.createIndex('pin_code', 'pin_code', {unique: true});
        }
    }
    openDBRequest.onsuccess = function (event) {
        let database = openDBRequest.result;
        if(!database.objectStoreNames.contains("centers")){
            indexedDB.deleteDatabase("cowin");
        }
    }
}

deleteIfOldDatabase();
initDB(DBConfig);

class App extends Component {

    state = {
        is_notification_granted: true,
        top_notification_content: "Grant notification permission manually, which will get use to notify if any slots available."
    }

    constructor(props) {
        super(props);
        this.OnNotificationPermissionChange = this.OnNotificationPermissionChange.bind(this);
    }

    componentDidMount(){
        if(!this.IsPushNotificationSupported()){
            this.setState({
                is_notification_granted: false,
                top_notification_content: "Notifications are not supported by your browser :("
            })
        }else{
            this.OnNotificationPermissionChange();
            if(Notification.permission === "default"){
                Notification.requestPermission(this.OnNotificationPermissionChange).then(r => {
                    // intentional
                });
            }
        }
    }

    OnNotificationPermissionChange(){
        if(Notification.permission === "denied"){
            this.setState({
                is_notification_granted: false
            })
        }
    }

    IsPushNotificationSupported() {
        return "serviceWorker" in navigator && "PushManager" in window;
    }

    render() {
        return (
            <div className="App">
                {
                    !this.state.is_notification_granted &&
                    <div className="bg-danger text-center text-white">
                        {this.state.top_notification_content}
                    </div>
                }
                <Router>
                    <Switch>
                        {/*        /!*<Redirect from={"/"} to={AppUrl.GET_VACCINATION_SESSION} exact={true} />*!/*/}
                        {/*        /!*<Route path={AppUrl.REGISTER_OTP} exact={true} component={RegisterOtp}/>*!/*/}
                        <Route path={""} exact={true} component={GetVaccineSession} />
                        {/*        /!*<Redirect to={AppUrl.GET_VACCINATION_SESSION} from={"*"}/>*!/*/}
                    </Switch>
                </Router>
            </div>
        );
    }
}

export default App;
